<template>
	<view>
		<!-- 顶部背景 -->
		<view class="bg-header"></view>
		<view class="page-container">
			<view class="welcome-text">
				Hi，欢迎登录
			</view>
			<view class="describe-text">
				请选择您的身份
			</view>
		</view>

		<view class="qiehuan">
			<view class="role-img" @click="selectRole(1)" :class="{ active: selectedRole === 1 }">
				<image :src="selectedRole === 1 ? '/static/image/role1-1.png' : '/static/image/role1.png'" mode="">
				</image>
				<view class="role-title">我是牧户</view>
			</view>
			<view class="role-img" @click="selectRole(2)" :class="{ active: selectedRole === 2 }"
				style="margin-left: 100rpx;">
				<image :src="selectedRole === 2 ? '/static/image/role2-2.png' : '/static/image/role2.png'" mode="">
				</image>
				<view class="role-title">我是采购员</view>
			</view>
		</view>

		<!-- 登录表单 -->
		<view class="login-form">
			<view class="form-item">
				<view class="form-label">手机号</view>
				<u-input class="form-input" type="text" v-model="form.username" placeholder="请输入手机号" />
			</view>
			<view class="form-item">
				<view class="form-label password-label">密码</view>
				<u-input class="form-input" type="password" v-model="form.password" placeholder="请输入密码" />
			</view>
		</view>

		<!-- 登录按钮 -->
		<view class="login-btn-wrapper">
			<button class="login-btn" @click="handleLogin">登录</button>
			<view class="register">
				没有账号？
				<text @click="registerClick">立即注册</text>
			</view>
		</view>
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				selectedRole: 1, // 默认选择牧户
				form: {
					username: '',
					password: '',
					roleType: 1 // 默认角色类型
				},
			}
		},
		methods: {
			// 跳转注册
			registerClick(){
				uni.showToast({
					title: '跳转注册',
					icon: 'none'
				});
				uni.navigateTo({
					url:"/pages/login/register/register"
				})
			},
			// 选择角色
			selectRole(role) {
				console.log("选择的角色为：", role)
				this.selectedRole = role;
				this.form.roleType = role;
			},
			// 登录方法
			handleLogin() {
				// 表单验证
				if (!this.form.username) {
					uni.showToast({
						title: '请输入手机号',
						icon: 'none'
					});
					return;
				}

				// 手机号码格式验证
				const phoneReg = /^1[3-9]\d{9}$/;
				if (!phoneReg.test(this.form.username)) {
					uni.showToast({
						title: '请输入正确的手机号码',
						icon: 'none'
					});
					return;
				}

				if (!this.form.password) {
					uni.showToast({
						title: '请输入密码',
						icon: 'none'
					});
					return;
				}

				// 这里可以调用登录接口
				// 示例：
				uni.showLoading({
					title: '登录中...'
				});

				setTimeout(() => {
					uni.hideLoading();
					// 模拟登录成功，实际开发中应该调用接口
					uni.showToast({
						title: '登录成功',
						icon: 'success',
						duration: 1500,
						success: () => {
							// 登录成功后直接跳转到首页，不调用任何方法
							setTimeout(() => {
								// 存储登录状态
								uni.setStorageSync('isLogin', true);
								uni.setStorageSync('userInfo', {
									username: this.form.username,
									roleType: this.form.roleType
								});

								// 使用reLaunch而不是switchTab，确保完全刷新页面
								uni.reLaunch({
									url: '/pages/tabbar/index/index'
								});
							}, 1500);
						}
					});
				}, 1000);
			}
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #ffffff;
	}
	.register{
		/** 文本1 */
		font-size: 26rpx;
		font-weight: 400;
		letter-spacing: 0rpx;
		line-height: 37.64rpx;
		color: #999999;
		display: flex;
		justify-content: center;
		margin-top: 50rpx;
		/** 文本2 */
		text{
			color: #3D8244;
		}
	}
	.qiehuan {
		display: flex;
		justify-content: center;
		margin-top: 66rpx;

		.role-img {
			width: 180rpx;
			display: flex;
			flex-direction: column;
			align-items: center;
			position: relative;

			image {
				width: 100%;
				height: 180rpx;
			}

			.role-title {
				font-size: 26rpx;
				font-weight: 400;
				letter-spacing: 0rpx;
				line-height: 37.64rpx;
				color: rgba(51, 51, 51, 1);
				text-align: center;
				margin-top: 10rpx;
			}
		}

		.role-img.active {
			position: relative;
		}
	}

	.bg-header {
		background: linear-gradient(171.27deg, rgba(223, 240, 221, 1) 0%, rgba(255, 255, 255, 1) 100%), linear-gradient(166.02deg, rgba(115, 196, 148, 1) 0%, rgba(209, 228, 210, 1) 32.07%, rgba(255, 255, 255, 1) 100%);
		left: 0rpx;
		top: -34rpx;
		width: 100%;
		height: 724rpx;
		opacity: 1;
		border-radius: 0rpx 0rpx, 16rpx, 16rpx;
		position: absolute;
		z-index: -1;
	}

	.page-container {
		padding: 0 60rpx;

		.welcome-text {
			opacity: 1;
			font-size: 52rpx;
			font-weight: 500;
			letter-spacing: 0rpx;
			line-height: 75.3rpx;
			color: rgba(19, 19, 19, 1);
			vertical-align: top;
			margin-top: 60rpx;
		}

		.describe-text {
			opacity: 1;
			font-size: 26rpx;
			font-weight: 400;
			letter-spacing: 0rpx;
			line-height: 37.64rpx;
			color: rgba(153, 153, 153, 1);
			text-align: left;
			vertical-align: top;
			margin-top: 12rpx;
		}
	}

	.login-form {
		padding: 0 60rpx;
		margin-top: 100rpx;

		.form-item {
			margin-bottom: 30rpx;
			display: flex;
			align-items: center;
			position: relative;

			.form-label {
				font-size: 28rpx;
				color: #333;
				width: 110rpx;
				flex-shrink: 0;
			}

			.password-label {
				// letter-spacing: 16rpx;
				/* 调整字间距使"密码"二字宽度接近"手机号"三字宽度 */
			}

			.form-input {
				flex: 1;
				// height: 90rpx;
				border-bottom: 1rpx solid #E5E5E5;
				font-size: 28rpx;
				color: #333;
				padding-left: 20rpx;
			}

			.password-eye {
				position: absolute;
				right: 10rpx;
				top: 50%;
				transform: translateY(-50%);
				z-index: 2;
				padding: 10rpx;
			}
		}
	}

	.login-btn-wrapper {
		padding: 0 60rpx;
		position: fixed;
		width: 100%;
		bottom: 20%;

		.login-btn {
			width: 100%;
			height: 90rpx;
			background: linear-gradient(237.74deg, rgba(114, 186, 112, 1) 0%, rgba(53, 122, 62, 1) 100%);
			color: #fff;
			font-size: 32rpx;
			border-radius: 45rpx;
			display: flex;
			align-items: center;
			justify-content: center;
		}
	}
</style>